img {
  max-width: 100%;
  height: auto;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #181824;
  font-family: "Roboto", "Arial", sans-serif;
  color: #333333;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  position: absolute;
  top: 50%;
  left: 50%;
  padding-bottom: 20px;
  transform: translateX(-50%) translateY(-50%);
  width: calc(100% - 40px);
  min-width: 240px;
  max-width: 600px;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0 8px 60px -10px rgba(95, 67, 164, 0.74);
}
.title {
  margin: 0;
  padding: 85px 0 0 0;
  color: #7157bd;
  font-size: 22px;
  font-weight: 700;
}
.photo {
  position: absolute;
  top: -60px;
  left: 50%;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #324e63;
  box-shadow: 0 5px 50px 0 #6c44fc, 0 0 0 7px rgba(107, 74, 255, 0.5);
  transform: translateX(-50%);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .photo {
    top: -75px;
    width: 150px;
    height: 150px;
  }
}
.photo__pic {
  display: block;
  margin: 0;
  padding: 0;
  width: 120px;
  height: 100%;
}
@media (min-width: 1200px) {
  .photo__pic {
    width: 150px;
  }
}
.page__header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.location {
  display: inline-flex;
  margin: 10px 0 0 0;
  padding: 0;
}
.location span {
  margin-left: 5px;
}
.part {
  display: flex;
  flex-direction: column;
  margin: 10px 20px 0 20px;
  padding: 10px;
  border-radius: 20px;
  background-image: linear-gradient(45deg, #1da1f2, #0e71c8);
  box-shadow: 0 4px 30px rgba(27, 86, 231, 0.7);
  overflow: hidden;
  height: 50px;
  transition: all 0.3s ease-in-out;
  color: transparent;
}
.part__title {
  margin: 0 auto;
  padding: 0;
  width: 100%;
  font-size: inherit;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border-radius: 16px;
}
.part__text {
  margin: 10px 0 0 0;
  padding: 0;
  font-size: 16px;
  line-height: 25px;
}
.about-me {
  background-image: linear-gradient(45deg, #1da1f2, #0e71c8);
  box-shadow: 0 4px 30px rgba(27, 86, 231, 0.7);
}
.my-art {
  background-image: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}
.js-active {
  background: none;
  border: 7px solid rgba(107, 74, 255, 0.5);
  color: #333333;
}
.js-active h2 {
  padding: 5px 15px;
  width: auto;
  border-radius: 16px;
  background: linear-gradient(45deg, #1da1f2, #0e71c8);
  box-shadow: 0 4px 15px #0164e687;
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  justify-content: center;
  margin: 20px;
  padding: 0;
  list-style: none;
}
.social__item {
  position: relative;
  margin: 0 10px 10px 10px;
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(23, 25, 86, 0.65);
}
.social__item--linkedin {
  background: linear-gradient(45deg, #003a92, #006cbd);
}
.social__item--git {
  background: linear-gradient(45deg, #333333, #626b73);
}
.social__item--instagram {
  background: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}
.social__item--vk {
  background: linear-gradient(45deg, #3b5998, #0078d7);
}
.social__link {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.social__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
